.btn {
  font-size: 14px;
  font-weight: 600;
  position: relative;
  margin: 0;
  z-index: 1;
  text-transform: initial;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
  padding: 8px 15px;
}
.btn:hover {
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.btn-white {
  background-color: #fff;
  border-color: #e7eaf3;
  color: #7539FF;
  box-shadow: inset 0 0 0 0 #fff;
}
.btn-white:hover {
  background-color: #fff;
  border-color: #fff;
  color: #fff;
  box-shadow: inset 0 50px 0 0 #7539FF;
}

.btn-white-outline {
  background-color: #fff;
  border-color: #e7eaf3;
  color: #3F4254;
}
.btn-white-outline:hover {
  background-color: #fff;
  border-color: #e7eaf3;
  color: #3F4254;
}

.btn-primary {
  background-color: #7539FF;
  border: 1px solid #7539FF;
  box-shadow: inset 0 0 0 0 #fff;
  border-radius: 4px;
}
.btn-primary:hover {
  background-color: #fff;
  border-color: #7539FF;
  color: #7539FF;
  box-shadow: inset 0 50px 0 0 #fff;
}

.btn-gray {
  background-color: #E0E0E0;
  border: 1px solid #E0E0E0;
  box-shadow: inset 0 0 0 0 #fff;
  color: #808080;
  padding: 8px 20px;
  border-radius: 4px;
}
.btn-gray:hover {
  background-color: #fff;
  border-color: #E0E0E0;
  color: #808080;
}

.btn-primary.tax {
  background-color: #E3E3E3;
  border: 1px solid #E3E3E3;
  box-shadow: inset 0 0 0 0 #fff;
  border-radius: 4px;
  color: #808080;
}
.btn-primary.tax:hover, .btn-primary.tax.active {
  background-color: #7539FF;
  border-color: #7539FF;
  color: #fff;
}

.btn-upload {
  background-color: #7539FF;
  border: 1px solid #7539FF;
  box-shadow: inset 0 0 0 0 #fff;
  color: #fff;
  border-radius: 4px;
  box-shadow: inset 0 50px 0 0 #7539FF;
}
.btn-upload:hover {
  background-color: #fff;
  border-color: #7539FF;
  color: #7539FF;
  box-shadow: inset 0 50px 0 0 #fff;
}

.btn-gray {
  background-color: #EFEFEF;
  border: 1px solid #EFEFEF;
  box-shadow: inset 0 0 0 0 #fff;
}
.btn-gray:hover {
  background-color: #fff;
  border-color: #EFEFEF;
  color: #878787;
  box-shadow: inset 0 50px 0 0 #fff;
}

.btn-gray-outline {
  background-color: #fff;
  border: 1px solid #EFEFEF;
  box-shadow: inset 0 0 0 0 #fff;
}
.btn-gray-outline:hover {
  background-color: #EFEFEF;
  border-color: #fff;
  color: #878787;
  box-shadow: inset 0 50px 0 0 #fff;
}

.btn-outline-primary {
  color: #7539FF;
  border-color: #7539FF;
}
.btn-outline-primary:hover {
  background-color: #7539FF;
  border-color: #7539FF;
}

.btn-check:focus + .btn-primary,
.btn-primary:focus {
  box-shadow: none !important;
}

.btn:focus {
  box-shadow: none !important;
}
.btn.btn-greys {
  background: #EFEFEF;
  color: #28084B;
  font-size: 14px;
  padding: 8px 16px;
  font-weight: 600;
}

