  background-color: #EADDFF;
  box-shadow: inset 0 0 0 0 #377dff;
  color: #7539FF;
  border-radius: 6px;
}
.cancel:hover {
  background-color: #7539FF;
  border: 1px solid #EADDFF;
  color: #EADDFF;
  box-shadow: inset 0 50px 0 0 #7539FF;
