  position: absolute;
  top: 0;
  left: 0;
  height: 19px;
  width: 21px;
  border: 1px solid #C4C4C4;
  background-color: #fff;
  border-radius: 6px;
  -webkit-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
.custom_check .checkmark::after {
  content: "\f00c";
