  cursor: pointer;
  user-select: none;
}
.custom_radio input, .custom_radio_one input {
  position: absolute;
  opacity: 0;
  padding: 0;
}
.custom_radio input:checked ~ .checkmark, .custom_radio_one input:checked ~ .checkmark {
  border: 1px solid #7539FF;
}
.custom_radio input:checked ~ .checkmark:after, .custom_radio_one input:checked ~ .checkmark:after {
  transform: scale(1);
  -webkit-transform: scale(1);
  -ms-transform: scale(1);
  opacity: 1;
  visibility: visible;
}
.custom_radio .checkmark, .custom_radio_one .checkmark {
  position: absolute;
  top: 2px;
  left: 0;
  height: 18px;
  width: 18px;
  background-color: transparent;
  border: 1px solid #cfcfcf;
  border-radius: 50%;
  -webkit-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.custom_radio .checkmark:after, .custom_radio_one .checkmark:after {
  display: block;
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  opacity: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #7539FF;
  visibility: visible;
  transform: scale(0.1);
  -webkit-transform: scale(0.1);
  -ms-transform: scale(0.1);
  -webkit-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

